'use strict';

layui.define([], function (exports) {
    exports("jlTool", {

        translatePHPJSON: function translatePHPJSON(str) {
            return $.parseJSON(str.replace(/\n/g, '\\n').replace(/\r/g, '\\r'));
        },
        sendIframeMsg: function sendIframeMsg(type, data) {
            if (window.parent.handleIframeMsg) {
                window.parent.handleIframeMsg(JSON.stringify({ type: type, data: data }));
            }
        },
        getQuery: function getQuery(search) {
            var str = search ? search : window.location.search;
            var res = {};
            if (str) {
                str = str.substr(1).split('&');
                $.each(str, function (index, value) {
                    var single = value.split('=');
                    res[single[0]] = single[1];
                });
            }
            return res;
        },
        urlEncode: function urlEncode(data) {
            var result = [];
            var checkType = function checkType(key, value) {
                if (value instanceof Object) {
                    for (var k in value) {
                        checkType(key + '[' + k + ']', value[k]);
                    }
                } else if (value !== undefined && value !== null) {
                    result.push(key + '=' + encodeURIComponent(value));
                }
            };
            for (var key in data) {
                checkType(key, data[key]);
            }
            return result.join('&');
        }
    });
});